import localData from "./data.js";

// export const base_url = "http://localhost:8000"
export const base_url = `${window.location.protocol}//${window.location.hostname}:${window.location.port}`
// export const base_url = `${window.location.protocol}//${window.location.hostname}:8000`

const dataSource = 'local';

const api_url = (dataSource === 'local') ?'':base_url+"/api";

const apis = {
    personal_info:api_url+"/personal_info",
    links:api_url+"/links",
    projects:api_url+"/projects",
    skills:api_url+"/skills",
    send_contact_message:api_url+"/send_contact_message",
    validate_pass_key:api_url+"/validate_pass_key",
    get_profiles:api_url+"/get_profiles",
}


export const getCookie = (cname) => {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(';');
    for(let i = 0; i <ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) == ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }


export const memoize = (fn) => {
    const cache = {} ;
    
    return async (...args) => {
        const url = args[0] ;
        const request = args[1] ;

        if (url in cache){
            // console.log('Fetching from cache .');
            return cache[url];
        
        }else{
            // console.log("Fetching from server .");
            const data = await fn(url,request);
            cache[url] = data;
            return data;
        }
    }
}


export const fetchData = memoize(async (url,request) => {

  if(dataSource === 'local'){
    return localData[url];
  }

    const token = window.sessionStorage.getItem("id_token");
    const pass_key = window.sessionStorage.getItem("pass_key");
    
    let body = request.body||{};
    body.token = token;
    body.pass_key = pass_key;
    body = JSON.stringify(body)

    let headers = request.headers || {};
    headers['X-CSRFToken'] = getCookie('csrftoken');

    request = {...request,headers,body};
    
    const response = await fetch(url,request);
    const data = await response.json();
    
    return data;
})


export default apis;
