import React, { useEffect, useState } from 'react'
import apis, { fetchData, base_url } from '../../utils/apis';

import emptyImage from '../../images/not_found.jpg'
import platforms from '../../utils/platforms'

import "./Projects.css"
import { useHistory, withRouter } from 'react-router-dom';

const Project = ({data}) => {
    const github = platforms['GH']
    const other = platforms['OT']
    return (
        <div className="project_item">
            <div className="top" >
                <img src={base_url+data?.project_pic||emptyImage} alt={data?.heading} />
                {/* <div className="description"><p>{data?.description}</p></div> */}
            </div>
            <h4 className="heading">{data?.heading}</h4>
            <div className="description"><p>{data?.description}</p></div>
            <div className="links">
                {
                    (data?.github_link) ? (
                        <a href={data?.github_link} className="icon_GH" target="_blank" referrer="">
                            <i className={github.icon}></i>
                        </a>
                    ) : ""
                }

                {
                   (data?.live_link) ? (
                        <a href={data?.live_link} className="icon_OT" target="_blank" referrer="">
                            <i className={other.icon}></i>
                        </a>
                    ) : ""
                }
            </div>
        </div>
    )
}

const Empty = () => {
    return (
        <div className="empty">
            <div className="left">
                <img src={emptyImage} alt="Not Found" className="empty_image" />
            </div>
        <div className="right">
            <h3><i className="far fa-frown"></i> <br /> No Projects Found</h3>
        </div>
        </div>
    )  
}

const Projects = (props) => {
    const [data , setData] = useState([]);
    const [nextURL, setNextURL] = useState(null);

    const history = useHistory()

    const goHome = () => {
        history.push("/");
    }

    const validateData = (resp) => {
        if(resp['token'] || resp['non_field_errors']){
            goHome();
        }else{
            setData([...data,...(resp?.results||[])]);
            setNextURL(resp?.next);
        }
    }
    
    const fetchProjects = (url) => {

        fetchData(url,{
            method:"POST",
            headers:{
                "content-type": "application/json",
            },
        },props).then(resp => {
            validateData(resp);
        })
    }

    useEffect(() =>{
        fetchProjects(apis.projects);
    },[])

    return (
        <div className="projects_container">
            
            {(data?.length > 0) ? (
                <div className="projects">
                    {data.map((item) => {
                        return <Project data={item}/>
                    })}
                </div>
            ) : (
                <Empty />
            )}

            <div className="actions">
                { nextURL ? (
                    <button className="load_more" onClick={()=>fetchProjects(nextURL)}>Load More</button>
                ) : ""}
            </div>
        </div>
    )
}

export default withRouter(Projects)