import React, {useState, useEffect} from 'react'
import {BrowserRouter as Router,Link, useHistory, withRouter} from 'react-router-dom'
import apis, { base_url ,fetchData} from '../../utils/apis';

import "./Navbar.css"

const NavItem = ({text,location}) => {
    let path = "/"+text.toLowerCase();
    if (text.toLowerCase() === "home"){
        path = "/";
    }

    return (
        <li className={`nav_action ${(location.pathname === path)?"nav_action_active":""}`}>
            <Link to={path}>{text}</Link>
        </li>
    )
}


const Navbar = (props) => {
    const [logo,setLogo] = useState("")
    const location = props.location;
    
    const history = useHistory()

    const goHome = () => {
        history.push("/");
    }

    const validateData = (resp,callback) => {
        if(resp['token'] || resp['non_field_errors']){
            goHome();
        }else{
            callback(resp);
        }
    }

    useEffect(()=>{
        
        fetchData(apis.personal_info,{
            method:'POST',
            headers:{"content-type":"application/json"},
        },props).then(data => {
            console.log(data)
            validateData(data,(data)=>setLogo(base_url+data?.icon));
        })

    },[])

    return (
        
        <div className="nav">
            <div className="left">
                <img src={logo} alt="logo" height="50" width="" onClick={goHome}/>
            </div>
            <div className="right">
                    <ul>
                        <NavItem text={"Home"} location={location} key={1}/>
                        <NavItem text={"About"} location={location} key={2}/>
                        {/* <NavItem text={"Skills"} location={location}/> */}
                        <NavItem text={"Projects"} location={location} key={3}/>
                        {/* <NavItem text={"Contact"} location={location}/> */}
                    </ul>
            </div>
        </div>
        
    )
}

export default withRouter(Navbar) 
