import React , {useState, useEffect}from 'react'
import "./About.css"

import apis, {base_url,fetchData} from "../../utils/apis"

import ProgressBar from '../../components/ProgressBar/ProgressBar';
import { useHistory } from 'react-router-dom';

const Skill = ({data}) => {
    return (
        <div className="skill_bar">
            <h4>{data?.heading} <span>{data?.skill_amount}%</span></h4>
            <ProgressBar value={data?.skill_amount||0}/>
        </div>
    )
}

const About = () => {
    const [skillData , setSkillData] = useState("");
    const [about , setAbout] = useState("");

    const history = useHistory()

    const goHome = () => {
        history.push("/");
    }

    const validateData = (resp,callback) => {
        if(resp['token'] || resp['non_field_errors']){
            goHome();
        }else{
            callback(resp);
        }
    }
    
    useEffect(() => {

        fetchData(apis.skills,{
            method: "POST",
            headers: {
                "content-type": "application/json"
            },
        }).then(data => validateData(data,setSkillData));

        fetchData(apis.personal_info,{
            method: "POST",
            headers: {
                "content-type": "application/json"
            },
        }).then(data => validateData(data,(data)=>setAbout(data?.skill_description)));
    
    },[])
    
    return (
        <div className="about">
            <div className="left about_info">
                <h4>About Myself</h4>
                <p>{about}</p>
            </div>
            <div className="right skills">
                {
                    skillData?.results?.map(skill => <Skill data = {skill}/>)
                }
            </div>
        </div>
    )
}

export default About
