import React,{useState, useEffect} from 'react'

import platforms from '../../utils/platforms'
import apis,{base_url,fetchData}from '../../utils/apis'

import "./Home.css"
import { useHistory, withRouter } from 'react-router-dom'

const IconLink = ({data}) => {

    const platform = data['platform'].slice(0,2)
    const link = data['link']
    const icon = platforms[platform].icon || "";

    return(
        <li className={`icon_${platform}`}>
            <a href={link} target="_blank" referrer="">
                <i className={icon}></i>
            </a>
        </li>
    )
}


const Home = (props) => {
    const [linksData, setLinksData] = useState("");
    const [personalData, setPersonalData] = useState("");

    const history = useHistory()

    const goHome = () => {
        history.push("/");
    }

    const validateData = (resp,callback) => {
        if(resp['token'] || resp['non_field_errors']){
            goHome();
        }else{
            callback(resp)
        }
    }

    useEffect(() =>{

        fetchData(apis.links,{
            method:'POST',
            headers:{
                'content-type': 'application/json'
            }
        },props).then(data => validateData(data,setLinksData));

        
        fetchData(apis.personal_info,{
            method: 'POST',
            headers: {
                'content-type': 'application/json'
            }
        },props).then(data => validateData(data,setPersonalData));

    },[])


    return (
        <div className="home">
            <div className="left">
                <img src={base_url+personalData?.profile_pic} alt={personalData?.name} />
            </div>
            <div className="right personal_info">
                <h6 className="greeting">Hello World, I am</h6>
                <h3 className="name">{personalData?.name}</h3>
                <h4 className="tagline">{personalData?.tagline}</h4>
                <p className="bio">
                    {personalData?.bio}
                </p>
                <ul className="list basic_info">
                    <li className="dob" key={1}>
                        <a href="#">
                        <i className="fas fa-birthday-cake"></i>
                        {personalData?.dob||"21st March 2002"}
                        </a>
                    </li>
                    <li className="phone" key={2}>
                        <a href="tel:{personalData?.mobile}">
                        <i className="fas fa-phone-alt"></i>
                        {personalData?.mobile}
                        </a>
                    </li>
                    <li className="email" key={3}>
                        <a href="mailto:nikhilthakur281@gmail.com" tager="_top">
                        <i className="fas fa-envelope"></i>
                        {personalData?.email}
                        </a>
                    </li>
                    <li className="address" key={4}>
                        <a href="#">
                        <i className="fas fa-map-marker-alt"></i>
                        {personalData?.address}
                        </a>
                    </li>
                </ul>
                <ul className="list social_links">
                    {
                        linksData?.results?.map(link => <IconLink data={link}/>)
                    }
                </ul>
            </div>
        </div>
    )
}

export default withRouter(Home)
